.top{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    z-index: 999;
}
.topleft,.topright
{
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;

}
.topIcons
{
font-size: 20px;
margin: 10px;
color: #444;
cursor: pointer;
}
.topimg
{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.topcenter
{
    flex: 6;
}
.toplist
{
    display: flex;
    align-items: center;
    list-style: none;
    justify-content: center;
   
}
.toplist li{
    margin: 20px;
    font-size: 18px;
    font-weight: 400;
    cursor: pointer;
}
.topsearchicon
{
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
}